module.exports = '.assetpicker-modal {\n    position: fixed;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    background: rgba(0, 0, 0, 0);\n    z-index: -1;\n    transition: background 0.2s, z-index 0s 0.2s;\n}\n.assetpicker-modal.assetpicker-modal-open {\n    z-index:3000;\n    background: rgba(0, 0, 0, 0.8);\n    transition: background 0.2s\n}\n.assetpicker-modal .assetpicker-modal-inner {\n    position: absolute;\n    background: #fff;\n    width: 800px;\n    height: 500px;\n    max-width: calc(100% - 30px);\n    max-height: calc(100% - 30px);\n    left:50%;\n    top:50%;\n    transform: translateY(-50%) translateX(-50%);\n    opacity: 0;\n    transition: opacity 0.1s 0.1s;\n    border-radius: 4px;\n    overflow: hidden;\n}\n.assetpicker-modal.assetpicker-maximized .assetpicker-modal-inner {\n    width: 100%;\n    height: 100%;\n}\n.assetpicker-modal.assetpicker-modal-open .assetpicker-modal-inner {\n    opacity: 1;\n}\n.assetpicker-modal .assetpicker-modal-inner iframe {\n    position: absolute;\n    background: transparent;\n    top:0;\n    left:0;\n    width:100%;\n    height: 100%;\n    overflow: hidden;\n    border: none;\n}\n\n.assetpicker-loader {\n    border: 5px solid gray;\n    border-radius: 30px;\n    height: 30px;\n    left: 50%;\n    margin: -15px 0 0 -15px;\n    opacity: 0;\n    position: absolute;\n    top: 50%;\n    width: 30px;\n\n    animation: assetpicker-loader-pulsate 1s ease-out;\n    animation-iteration-count: infinite;\n}\n.assetpicker-ready .assetpicker-loader {\n    display: none;\n    animation: none;\n}\n\n@keyframes assetpicker-loader-pulsate {\n    0% {\n        transform: scale(.1);\n        opacity: 0.0;\n    }\n    50% {\n        opacity: 1;\n    }\n    100% {\n        transform: scale(1.2);\n        opacity: 0;\n    }\n}';